export default {
	data() {
		return {
			
		}
	},
	methods: {
		logout() {
		   const that = this;
		   uni.showModal({
		   	 title:"提示",
			 content:"您确定退出？",
			 success: function (res) {   
				if (res.confirm) {
					that.$http.requestGet({
						url:`${that.$baseUrl}/api/user/logout`,
						success: res => {
							/* uni.removeStorageSync('token'); */
							uni.showToast({
								title:"注销成功",
								icon:'none',
								duration: 2000
							})
						}
					})
				} else if (res.cancel) {
					console.log('用户点击取消');
				}
			 }
		   })
		}
	}
}
